.items {
  width: 100%;
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.itemContainer {
  display: flex;
  flex-direction: column;
  background-color: var(--bg-overlay-light);
  border-radius: .4em;
  transition: all 250ms;
  overflow: hidden;
}

.itemContainer:not(:last-child) {
  margin-bottom: 10px;
}


.itemCard {
  padding: .8em;
  display: flex;
  flex-direction: column;
  row-gap: 1em;
  flex: 1 1 auto;
  background-color: var(--bg-overlay);
  border-radius: .4em;
  transition: all 250ms;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.2);
}

.itemDetails span {
  font: 0.9rem monospace;
  opacity: 0.6;
}

.itemTitle {
  font-size: 1.2rem;
}

.itemNames {
  padding: 0;
  margin: 0;
  margin-top: .3em;
  display: flex;
  flex-wrap: wrap;
  column-gap: .5em;
  row-gap: .8em;
  list-style-type: none;
}

.itemNamesInput {
  display: none;
}

.itemNamesLabel {
  cursor: pointer;
  user-select: none;
  padding: 0.3em 0.3em 0.2em;
  background-color: var(--bg-overlay);
  border-radius: 0.3em;
  transition: all 100ms;
}

.itemNamesLabel:hover {
  background-color: var(--bg-overlay-hover);
}

:checked~.itemNamesLabel {
  background-color: rgba(255, 255, 255);
  color: #333;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
}

.itemActions {
  padding: .5em;
  padding-left: .8em;
  display: flex;
  gap: .5em;
  /* flex-direction: column; */
  /* align-items: center; */
  /* justify-content: flex-start; */
  /* row-gap: 0.5em; */
}

.itemAction {
  cursor: pointer;
  /* width: 2.5em; */
  /* aspect-ratio: 1; */
  display: grid;
  place-items: center;
  /* background-color: var(--bg-overlay); */
  background: none;
  border: none;
  border-radius: .4em;
  opacity: 0.7;
}

.itemAction:hover,
.itemAction:hover * {
  /* background-color: var(--bg-overlay-hover); */
  opacity: 1 !important;
}

/* .itemAction:active { */
/* background-color: var(--bg-overlay-active); */
/* opacity: 1; */
/* } */

.itemAction * {
  /* padding: .11em .18em .08em; */
  text-decoration: underline;
  font-size: 1rem;
}


.success.itemContainer {
  background-color: var(--bg-success-secondary);
}

.success .itemCard {
  background-color: var(--bg-success);
}

.success .itemCard * {
  color: var(--color-success);
}

.success .itemAction,
.success .itemAction * {
  color: var(--color-success);
  opacity: 0.8;
}